import { createRouter, createWebHashHistory } from 'vue-router'
import Layout from './Layout.vue'
import startPage from './startPage.vue'
import tableLevel from './tableLevel.vue'
import tableB30 from './tableB30.vue'

const routes = [
  {
    path: '/',
    component: Layout,
    children: [
      {
        path: '',
        name: 'start',
        component: startPage
      },
      {
        path: 'tableLevel',
        name: 'tableLevel',
        component: tableLevel
      },
      {
        path: 'tableB30',
        name: 'tableB30',
        component: tableB30
      }
    ]
  },
  {
    path: '/:pathMatch(.*)*',
    redirect: { name: 'start' }
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes,
  scrollBehavior() {
    return { top: 0 }
  }
})

export default router